import { Link } from "gatsby";
import React from "react";
import { useStaticQuery, graphql } from "gatsby";
// import { products } from "../../mockData";

const ProductCard = ({ data, index }) => {
  const queryData = useStaticQuery(graphql`
    query MyQuery {
      allFile {
        edges {
          node {
            publicURL
            sourceInstanceName
            relativePath
          }
        }
      }
    }
  `);

  let productImg = queryData.allFile.edges.find((file) =>
    file.node.publicURL.endsWith(data.outerImg)
  );

  return (
    <>
      <Link to={`/products/${data?.id}`}>
        <div className='product-card-wrapper' key={index}>
          <div className='product-card-cover'>
            <img src={productImg?.node?.publicURL} alt='Tiles' />
          </div>
          <div className='card-desc'>
            <h6>{data?.name}</h6>
          </div>
        </div>
      </Link>
    </>
  );
};

export default ProductCard;
